import aboutBgImg from "../../../assets/img/bg/about-bg.png";
import video_s from "../../../assets/video/arch_games.mp4";
import SectionTitle from "../SectionTitle/SectionTitle";

const About = () => {
  return (
    <section
      id="about"
      className="pt-70 pb-140 p-relative"
      style={{
        background: `url(${aboutBgImg}) no-repeat center center / cover`,
      }}
    >
      <div className="container">
        <div className="row align-items-center">
          <div className="col-lg-6 col-md-12 col-sm-12">
            <video src={video_s} autoPlay loop muted></video>
          </div>
          <div className="col-lg-6 col-md-12 col-sm-12">
            <div
              className="about-content s-about-content wow fadeInRight"
              data-wow-delay=".4s"
            >
              <SectionTitle titlefirst={"About"} titleSec={"Us"} />
              <p>
                Arch Software is a forward-thinking technology solutions
                provider dedicated to crafting high-quality applications,
                immersive games, and powerful automation tools. Our team of
                experienced developers, designers, and engineers is committed to
                leveraging cutting-edge technologies and best practices to
                deliver solutions that propel businesses and individuals toward
                success.
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;
