import React, { useState } from "react";
import { Link } from "react-router-dom";

const Offcanvas = ({ onOpen, onClose, menuRouters, handleLinkClick }) => {
  const [searchText, setSearchText] = useState("");
  return (
    <>
      <div className={`offcanvas-menu ${onOpen ? "active" : ""}`}>
        <span className="menu-close" onClick={() => onClose(!onOpen)}>
          <i className="fas fa-times"></i>
        </span>
        <form role="search" id="searchform" className="searchform">
          <input
            type="text"
            name="s"
            id="search"
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
            placeholder="Search"
          />
          <button>
            <i className="fa fa-search"></i>
          </button>
        </form>
        <nav className="tablet-mobile-menu">
          <ul>
            {menuRouters.map((item, i) => (
              <li key={i} className={item.active && "menu-active"}>
                <Link onClick={() => onClose(!onOpen)} to={item.path}>
                  {item.title}
                </Link>
              </li>
            ))}
          </ul>
        </nav>
      </div>
      <div
        className={`offcanvas-overly ${onOpen ? "active" : ""}`}
        onClick={() => onClose(!onOpen)}
      ></div>
    </>
  );
};

export default Offcanvas;
