import React, { useEffect } from "react";
import { observer } from "mobx-react-lite";

import Breadcrumb from "../../Utilities/Breadcrumb/Breadcrumb";
import InfoBlock from "../../Utilities/InfoBlock";

import pageDataStore from "../../../stores/pageDataStore";

const BlogPost = () => {
  const { getServices, services } = pageDataStore;

  useEffect(() => {
    getServices();
  }, []);

  return (
    <div className="breadcrumb-wrapper container">
      <Breadcrumb pageTitle={"Services"} currentPage={"Services"} />
      <div className="info-list">
        {services?.map((service) => (
          <InfoBlock
            titleFirst={service?.title_first}
            titleSecond={service?.title_second}
            description={service?.main_description}
            order_list={service?.order_list}
          />
        ))}
        <div className="container">
          <div className="slider-btn">
            <a
              href="https://docs.google.com/forms/d/e/1FAIpQLScHiVaf7WHQVXEk-arCjFqxGP4LJOAqKU-PqCRpZZUBCoSVbA/viewform"
              className="btn ss-btn mt-20 green-btn"
              rel="noreferrer"
              target="_blank"
            >
              Begin Project
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
export default observer(BlogPost);
