import React, { useMemo, useState } from "react";
import { Link } from "react-router-dom";

import Logo from "../Logo/Logo";
import logo from "../../../assets/img/logo/logo-new.png";
import Offcanvas from "../Offcanvas/Offcanvas";
import { useLocation } from "react-router-dom/cjs/react-router-dom";

const Header = () => {
  const location = useLocation();
  const [openMenu, setOpenMenu] = useState(false);

  const handleToggleMenu = () => {
    setOpenMenu(!openMenu);
  };

  const menuRouters = useMemo(() => {
    return [
      {
        path: "/",
        title: "Home",
        active: location.pathname === "/",
      },
      {
        path: "/about",
        title: "About Us",
        active: location.pathname.includes("/about"),
      },
      {
        path: "/service",
        title: "Services",
        active: location.pathname.includes("/service"),
      },
      {
        path: "/product",
        title: "Products",
        active: location.pathname.includes("/product"),
      },
      {
        path: "/contact",
        title: "Contact Us",
        active: location.pathname.includes("/contact"),
      },
    ];
  }, [location.pathname]);

  const handleLinkClick = ({ index, close }) => {
    // setMenuRouters((prevRouters) =>
    //   prevRouters.map((router, i) => ({
    //     ...router,
    //     active: i === index,
    //   }))
    // );
    // close && setOpenMenu(!openMenu);
  };

  return (
    <React.Fragment>
      <header className="header">
        <div className="header-container">
          <div className="header-logo-menu">
            <div className="header-logo">
              <Logo logo={logo} />
            </div>
            <nav id="mobile-menu" className="header-main-menu">
              <ul>
                {menuRouters.map((item, i) => (
                  <li key={i} className={item.active && "menu-active"}>
                    <Link to={item.path}>{item.title}</Link>
                  </li>
                ))}
              </ul>
            </nav>
            <div className="burger-menu-icon">
              <p onClick={handleToggleMenu} className="menu-tigger">
                <i className="fa fa-bars"></i>
              </p>
            </div>
          </div>
        </div>
      </header>
      <Offcanvas
        onOpen={openMenu}
        onClose={setOpenMenu}
        menuRouters={menuRouters}
        handleLinkClick={handleLinkClick}
      />
    </React.Fragment>
  );
};

export default Header;
